import { mergeProps } from '../utils/UNSAFE_mergeProps';
import { Size } from '../utils/UNSAFE_size';
import { classNames } from '../utils/UNSAFE_classNames';
import { useTranslationBundle } from '../hooks/UNSAFE_useTranslationBundle';
import type { BundleType } from '../resources/nls/bundle';
import { ComponentMessageItem } from '../UNSAFE_ComponentMessage';
import { cssProps } from '@oracle/oraclejet-cssprops';

import { getDefaultTriggerContent, getAriaLabel, getStyles } from './FilePickerUtils';
import { useFilepickerElementHelper } from './useFilePickerElement';

import { ComponentChildren } from 'preact';
import { useState, useRef, useImperativeHandle } from 'preact/hooks';
import { ForwardedRef } from 'preact/compat';
import { useTabbableMode } from '../hooks/UNSAFE_useTabbableMode';

// TODO: Andy's comment below JET-53230
// The FilePicker supports two very different use cases: default and custom file pickers. Should we consider dropping the wrapper and instead exporting the two underlying components (with the appropriate property types) directly?
// If not, perhaps we should look into defining the Props type as a discriminated union such that primaryText/secondaryText props are only valid when children is not specified?

type Props = {
  /**
   * An array of strings of allowed MIME types or file extensions that can be uploaded; this is unlike the accept attribute of the html &lt;input> element that accepts a simple comma-delimited string. If not specified, all file types will be accepted.
   * <p>Note: If accept is specified, files with empty string type will be rejected if no match found in the "accept" value.   */
  accept?: string[];

  /**
   * Specifies the preferred facing mode for the device's
   * <a href="https://www.w3.org/TR/html-media-capture/#dom-htmlinputelement-capture">media capture</a> mechanism;
   * This is most often used to provide direct camera access on mobile devices.  Note that the accept attribute must
   * be specified and have an associated capture control type (e.g.["image/*"]) for the capture attribute to take effect.  Support may vary by browser.
   *
   */
  capture?: 'user' | 'environment' | 'implementation' | 'none';

  /**
   * Disables the filepicker if set to <code class="prettyprint">true</code>.
   */
  isDisabled?: boolean;

  /**
   * The primary text for the default file picker.
   */
  primaryText?: string;

  /**
   * The secondary text for the default file picker.
   */
  secondaryText?: string;
  /**
   * Whether to allow single or multiple file selection.
   */
  selectionMode?: 'multiple' | 'single';

  /**
   * A label to be used for accessibility purposes.
   */
  accessibleLabel?: string;

  /**
   * Property that triggers a callback immediately when a set of files have been committed
   */
  onCommit?: (detail: CommitDetail) => void;

  /**
   * Property that triggers a callback immediately when a set of files have been rejected
   */
  onReject?: (detail: RejectDetail) => void;

  /**
   * Width of the File Picker
   */
  width?: Size;

  /**
   * Height of the File Picker
   */
  height?: Size;

  /**
   * If the children is provided, its contents replace the default filepicker.  Filepicker will still set role and aria-label, but
   * the application is responsible for setting the tabindex.
   */
  children?: ComponentChildren;
};

export type CommitDetail = {
  files: FileList;
};

export type RejectDetail = {
  /**
   * Messages that should be displayed to the user (e.g. in an oj-messages component) describing rejected files.
   */
  messages: ComponentMessageItem[];

  /**
   * This property may be populated with a Promise to provide short-term feedback during a user interaction (typically drag and drop); the feedback should be cleared upon the resolution of this Promise.
   */
  until: Promise<void> | null;
};

/**
 * A file picker displays a clickable dropzone for selecting files from the device storage.
 */
export function FilePicker({
  capture = 'none',
  isDisabled = false,
  selectionMode = 'multiple',
  children,
  ...otherProps
}: Props) {
  // this ref is added only for testing purposes.  Allows the test to directly
  // call certain functions that should be tested
  const testHandlersRef = (otherProps as any).__testHandlerSymbol;
  const translations = useTranslationBundle<BundleType>('@oracle/oraclejet-preact');
  const props = { capture, selectionMode, translations, ...otherProps };
  if (isDisabled) {
    return <DisabledFilePicker {...props}>{children}</DisabledFilePicker>;
  }
  return children ? (
    <CustomFilePicker testHandlersRef={testHandlersRef} {...props}>
      {children}
    </CustomFilePicker>
  ) : (
    <DefaultFilePicker testHandlersRef={testHandlersRef} {...props} />
  );
}

type FilePickerProps = Props & {
  translations: BundleType;
};

type DndProps = {
  onDragEnter: (event: Event) => void;
  onDragOver: (event: DragEvent) => void;
  onDragLeave: (event: DragEvent, mimeTypeDropFail: boolean) => void;
  onDrop: (event: DragEvent) => void;
};

export type TestHandlersRef = ForwardedRef<
  DndProps & {
    onClickSelected: (files: FileList) => void;
  }
>;

type TestHandlerProps = {
  // this ref is populated only in tests
  testHandlersRef?: TestHandlersRef;
};

type DefaultProps = Omit<FilePickerProps, 'isDisabled' | 'children'> & TestHandlerProps;

/**
 * The default file picker element.
 */
const DefaultFilePicker = (props: DefaultProps) => {
  const [validity, setValidity] = useState<'NA' | 'valid' | 'invalid'>('NA');

  // class added to change bg/border color based on if file is valid on drag and drop
  const validityClass =
    validity === 'valid' ? 'oj-c-valid-drop' : validity === 'invalid' ? 'oj-c-invalid-drop' : '';
  const rootRef = useRef<HTMLDivElement>(null);

  const { validation, dragAndDropProps, pressProps, isHover, hoverProps } =
    useFilepickerElementHelper(props, rootRef, setValidity);
  const mergedEventProps = mergeProps(pressProps, hoverProps);

  // These are backdoor handles that are used for testing purposes only
  useSetupTestRef(dragAndDropProps, validation, props.testHandlersRef || null);
  const classes = classNames([
    styles.base,
    props.width === undefined && styles.maxWidth,
    isHover && styles.hover
  ]);
  const { isTabbable } = useTabbableMode();
  const {
    accessibleLabel,
    primaryText,
    secondaryText,
    translations,
    selectionMode,
    width,
    height
  } = props;
  return (
    <div
      {...mergedEventProps}
      class={classes}
      tabIndex={isTabbable ? 0 : -1}
      role="button"
      aria-label={getAriaLabel({
        accessibleLabel,
        isDisabled: false,
        translations,
        primaryText,
        secondaryText,
        selectionMode
      })}
      ref={rootRef}
      style={getStyles(width, height)}>
      <div class={classNames([styles.inner, validityClass])} {...dragAndDropProps}>
        {getDefaultTriggerContent(false, primaryText, secondaryText, translations, selectionMode)}
      </div>
    </div>
  );
};

type DisabledProps = Omit<
  FilePickerProps,
  'accept' | 'capture' | 'onCommit' | 'onReject' | 'isDisabled'
>;

/**
 * The disabled file picker element.
 */
const DisabledFilePicker = ({
  children,
  accessibleLabel,
  primaryText,
  secondaryText,
  translations,
  selectionMode,
  width,
  height
}: DisabledProps) => {
  return (
    <div
      class={classNames([
        childrenStyles,
        styles.base,
        styles.inner,
        width === undefined && styles.maxWidth,
        'oj-c-filepicker-disabled'
      ])}
      role="button"
      aria-label={getAriaLabel({
        accessibleLabel,
        isDisabled: true,
        translations,
        primaryText,
        secondaryText
      })}
      aria-hidden="true"
      style={getStyles(width, height)}>
      {children ||
        getDefaultTriggerContent(true, primaryText, secondaryText, translations, selectionMode)}
    </div>
  );
};

type CustomProps = Omit<FilePickerProps, 'isDisabled' | 'primaryText' | 'secondaryText'> &
  TestHandlerProps;

/**
 * The custom file picker element.
 */
const CustomFilePicker = (props: CustomProps) => {
  const rootRef = useRef<HTMLDivElement>(null);
  const { validation, dragAndDropProps, pressProps, isHover, hoverProps } =
    useFilepickerElementHelper(props, rootRef);
  const mergedEventProps = mergeProps(pressProps, dragAndDropProps, hoverProps);

  // These are backdoor handles that are used for testing purposes only
  useSetupTestRef(dragAndDropProps, validation, props.testHandlersRef || null);

  const classes = classNames([
    styles.base,
    props.width === undefined && styles.maxWidth,
    isHover && styles.hover
  ]);
  const { accessibleLabel, translations, selectionMode } = props;
  const { isTabbable } = useTabbableMode();
  return (
    <div
      class={classNames([classes, childrenStyles])}
      tabIndex={isTabbable ? undefined : -1}
      role="button"
      aria-label={getAriaLabel({ accessibleLabel, isDisabled: false, translations, selectionMode })}
      ref={rootRef}
      style={getStyles(props.width, props.height)}
      {...mergedEventProps}>
      {props.children}
    </div>
  );
};

/**
 * Only called for testing purposes, sets up the test ref that
 * allows for backdoor access to certain methods
 */
const useSetupTestRef = (
  dragAndDropProps: DndProps,
  validation: (files: FileList) => void,
  testHandlersRef: TestHandlersRef
) => {
  useImperativeHandle(testHandlersRef, () => ({
    onDragEnter: dragAndDropProps.onDragEnter,
    onDragOver: dragAndDropProps.onDragOver,
    onDrop: dragAndDropProps.onDrop,
    onDragLeave: dragAndDropProps.onDragLeave,
    onClickSelected: validation
  }));
};

const styles = {
  base: cssProps`${{
    display: 'block',
    height: '100%',
    width: '100%'
  }}`,
  maxWidth: cssProps`${{
    maxWidth: '400px'
  }}`,
  inner: cssProps`${{
    justifyContent: 'space-between',
    alignItems: 'center',
    boxSizing: 'border-box',
    borderRadius: 'var(--oj-c-PRIVATE-DO-NOT-USE-file-picker-border-radius)',
    paddingTop: 'var(--oj-c-PRIVATE-DO-NOT-USE-file-picker-padding)',
    paddingRight: 'var(--oj-c-PRIVATE-DO-NOT-USE-file-picker-padding)',
    paddingLeft: 'var(--oj-c-PRIVATE-DO-NOT-USE-file-picker-padding)',
    paddingBottom: 'var(--oj-c-PRIVATE-DO-NOT-USE-file-picker-padding)',
    height: '100%',
    borderStyle: 'dashed',
    borderWidth: 'var(--oj-c-PRIVATE-DO-NOT-USE-file-picker-border-width)',
    backgroundColor: 'var(--oj-c-PRIVATE-DO-NOT-USE-file-picker-bg-color)',
    borderColor: 'var(--oj-c-PRIVATE-DO-NOT-USE-file-picker-border-color)',
    '&:active': { backgroundColor: 'var(--oj-c-PRIVATE-DO-NOT-USE-core-bg-color-active)' }
  }}`,
  hover: cssProps`${{
    // @ts-ignore
    '&:not(:active)': {
      cursor: 'var(--oj-c-PRIVATE-DO-NOT-USE-file-picker-cursor)'
    }
  }}`,

  pseudohover: cssProps`${{
    // @ts-ignore
    '@media(hover: hover)': {
      '&:hover:enabled:not(:active)': {
        cursor: 'var(--oj-c-PRIVATE-DO-NOT-USE-file-picker-cursor)'
      }
    }
  }}`
};
const childrenStyles = cssProps`${{
  /* @ts-ignore */
  '& > *': {
    marginTop: '0 !important',
    marginBottom: '0 !important',
    marginLeft: '0 !important',
    marginRight: '0 !important'
  }
}}`;
